@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
#app{text-align:center}body{text-align:center;background-color:rgba(223,210,32,0.623);font-family:'Roboto Mono', monospace}#header{font-size:30px;color:black;text-align:center;font-weight:bold;margin-bottom:100px}#drum-machine{width:50%;margin:auto;border-color:black;border-width:10px;border-style:solid;padding:10px;background-color:grey}#inner-drum-machine-box{border-color:white;border-width:5px;border-style:solid;padding:25px;background-color:crimson}#display{font-size:14px;font-weight:bold;text-align:center;padding:25px}#display-container{background-color:white;margin-left:15px;margin-right:15px;margin-top:-5px;border-style:solid;border-color:purple;border-width:4px;padding:3px}.label-title{font-weight:bold;font-style:italic;font-size:19px}.power-button{font-weight:bolder;font-size:23px;background-color:#b9b0b0;margin-left:80px;margin-right:80px;border-style:solid;border-color:black;border-width:5px;padding:10px;transition:ease-out 0.2s;-webkit-user-select:none;-ms-user-select:none;user-select:none}.power-button:hover{background-color:yellow;cursor:pointer}#power-button-off{color:black}#power-button-on{color:blue}#volume-control-container{background-color:#b9b0b0;margin-left:30px;margin-right:30px;border-style:solid;border-color:black;border-width:3px;padding:5px}#volume{width:75%}.col{padding:10px}.drum-pad{text-align:center;display:inline-block;font-size:125%;font-weight:bold;padding:25px 35px;border-style:ridge;border-color:black;border-width:2.5px;border-radius:10px;background-color:aquamarine;transition:ease-out 0.4s;-webkit-user-select:none;-ms-user-select:none;user-select:none}.drum-pad:hover{background-color:royalblue;cursor:pointer}.footer{position:absolute;text-align:center;width:100%;bottom:0;padding-top:5rem}

